























import HeadTtop from '../../../../components/head/head.vue'

export default {
	components: {
		'HeadTtop': HeadTtop
	},
	data() {
		return {
			Title: '修改密码',
			backshow: true,
			topsrc: '../../../../static/images/back.png',
			password: '',
			oldPass: '',
			confirmPass: '',
			tel:''
		}
	},
	onLoad() {
		this.tel = uni.getStorageSync('userinfo').tel
	},
	methods: {
		suremodifys() {
			if( this.password.length < 6 ){
				uni.showToast({
					title: '密码长度大于6位',
					duration: 1000,
				});
				return false
			}
			var that = this;
			let params = {
				page: this.page,
				password: this.password,
				oldPass: this.oldPass,
				confirmPass: this.confirmPass,
				_token: uni.getStorageSync('userinfo')._token
			}
			
			this.$http.HttpRequst.request(false, 'user/editPass', params, 'POST', res => {
				console.log( res )
				uni.showToast({
					title: res.msg,
					duration: 1000,
					success() {
						setTimeout(function() {
							uni.navigateBack();
						}, 1000)
					}
				});
			});
		}
	}
}
